;(function($) {

    $.fn.equalHeight = function(options) {
        var settings = $.extend({
            resize : false
        }, options || {});

        var self = this;

        equalize();

        if (settings.resize) {
            $(window).on('resize', function() {
                equalize();
            });
        }

        function equalize() {
            var tallestcolumn = 0;

            self.removeAttr('style');

            self.each(function() {
                var currentHeight = $(this).height();

                if(currentHeight > tallestcolumn) {
                    tallestcolumn  = currentHeight;
                }
            });

            self.height(tallestcolumn);
        }

        return this;
    }

})(jQuery);